                  <!--<funnel :params="djjgFunnelParams"></funnel>-->
                <!--</div>-->
                <!--<div class="chart">-->
                  <!--<ctitle :title="'年度信访目的占比'" class="r-b-title"></ctitle>-->
                  <!--<pie-chart3D :params="xfmdPieParams" style="margin-top: -5%;"></pie-chart3D>-->
                  <!--<img src="../../assets/hot-pie-bg.png" alt="" class="hot-bg">-->
                <!--</div>-->

              <!--</el-carousel-item>-->
              <!--&lt;!&ndash;<el-carousel-item&ndash;&gt;-->
                <!--&lt;!&ndash;class="carousel-item"&ndash;&gt;-->
                <!--&lt;!&ndash;:key="2"&ndash;&gt;-->
              <!--&lt;!&ndash;&gt;&ndash;&gt;-->
                <!--&lt;!&ndash;<div class="chart">&ndash;&gt;-->
