<script>
  import {get_http} from "@/api/index";
  import {gradientColor} from "@/utils/gradient";
  import ctitle from "@/components/title/index";
  import Map from "@/components/map/Map";
  import loopPie from "@/components/pie/LoopPie";
  import funnel from "@/components/funnel/Funnel";
  import PieChart3D from "@/components/pie/PieChart3D";
  import incNumber from "@/components/incNumber/index";
  import horizontalBar from "@/components/bar/HorizontalBar";
  import LinesR from "@/components/line/LinesR";
  import cirCircle from "@/components/cirCircle/index";

  export default {
    name: "home",
    components: {
      ctitle,
      Map,
      loopPie,
      funnel,
      PieChart3D,
      incNumber,
      horizontalBar,
      LinesR,
      cirCircle
    },
    data() {
      return {
        //区域年度信访量排行
        qyphBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          }
        },
        //地图左上角(件次总数)
        jctotal: 0,
        //地图左上角(各类件次)
        jcType: [],
        //地图
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "100%",
          },
          data: [],
          options: {
            isHome: true,
            JSON: "hebei",
            xName: "name",
            yName: "value_g0_1",
            map: {
              "value_g0_1": "总数",
              "value_g1_1": "来信",
              "value_g1_2": "来访",
              "value_g1_3": "网上投诉"
            },
            carousel: true
          }
        },
        //信访量月度走势
        ydLinesParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          colors: [
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            splitNumber:4,
            axisLabelColor: "#fff",
            yaxisLabelColor: "#82d8ff",
            legendfontSize: 18,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "月",
            axisYName:"件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            left: "10%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "5%",
            legendRight: "10%",
          },
          map: {
            value_g0_1: "今年",
            value_g1_1: "去年"
          },
          xName: "name"
        },
        // 年度初件重件占比
        cjcjPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["44%", "52%"],
            center: ["50%", "50%"],
            centerRadius: ["39%", "40%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.2)
            },
            length: 20,
            length2: 70,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            tooltip: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            threeLevel: true,
            rollFlag: true
          }
        },
        // 年度信访形式占比
        xfxsPieParams: {
          data: [],
          // title: "年度信访量信访目的占比",
          color: [
            "#3aa3bf",
            "#5068bf",
            "#33aedf"
          ],
          styleObj: {
            width: "100%",
            height: "100%"
          },
          angle: 20,
          radius: "23%",
          labelRadius: 5,
          isBr: true,
          labelPer: true,
        },
        // 年度各级登记机构占比
        djjgFunnelParams: {
          canvasStyle: {
            width: "100%",
            height: "90%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.gradientColors,
            tooltipShow:true,
            // title: "年度信访量各级登记机构占比",
            sort: "none",
            rollFlag: true,
            top: "15%",
            borderTop: "16%",
            borderBot: "0%",
            bottom: "0%",
            labelLineLen: 56
          }
        },
        //年度初次信访件化解率
        hjvValue: 0,
        // 年度热点问题分析
        rdwtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.pieColors,
            title: "年度热点问题占比",
            radius: ["50%", "60%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            tooltip: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
          }
        },
        // 年度信访目的占比
        xfmdPieParams: {
          data: [],
          title: "年度信访量信访目的占比",
          color: this.$STANDARD.pieColors,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          radius: "23%",
          labelRadius: 5,
          isBr: true,
          labelPer: true,
          angle: 30
        }
      };
    },
    mounted() {
      console.log(this.$STANDARD);
      this.getAPI(this.year);
    },
    methods: {
      getAPI(year) {
        // 1区域年度信访量排行（左侧柱图）
        get_http("homeLeftBar", {year: year})
          .then(res => {
            this.qyphBarParams.data = res.data.data.sort((a, b) => {return a.value - b.value;});
          })
          .catch(err => {
            console.log(err);
          });
        // 2地图
        get_http("homeMap", {year: year})
          .then(res => {
            this.mapParams.options.map = res.data.map;
            this.mapParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });

        // 3全省信访量-件次总数（地图左上角）
        get_http("homeJctotal", {year: year})
          .then(res => {
            this.jctotal = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });

        // 4全省信访量-各类件次（地图左上角）
        get_http("homeJcType", {year: year})
          .then(res => {
            this.jcType = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });

        // 5信访量月度走势
        get_http("homeYdzsLine", {year: year})
          .then(res => {
            this.ydLinesParams.map = res.data.map;
            this.ydLinesParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });

        // 6年度初件重件占比
        get_http("homeCjcjPie", {year: year})
          .then(res => {
            this.cjcjPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });

        // 7年度信访形式占比
        get_http("homeXfxsPie", {year: year})
          .then(res => {
            this.xfxsPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });

        // 8年度各级登记机构占比
        get_http("homeDjjgFunnel", {year: year})
          .then(res => {
            this.djjgFunnelParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });

        // 9年度初次信访件化解率
        // get_http("homeHjvValue", {year: year})
        //   .then(res => {
        //     this.hjvValue = (res.data.data[0].value * 100).toFixed(2);
        //   })
        //   .catch(err => {
        //     console.log(err);
        //   });

        // 10年度热点问题分析
        // get_http("homeRdwtPie", {year: year})
        //   .then(res => {
        //     this.rdwtPieParams.data = res.data.data;
        //   })
        //   .catch(err => {
        //     console.log(err);
        //   });

        // 11年度信访目的占比
        get_http("homeXfmdPie", {year: year})
          .then(res => {
            this.xfmdPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      },
      // 日期填充0
      zeroPadding(num, digit) {
        let zero = '';
        for (let i = 0; i < digit; i++) {
          zero += '0';
        }
        return (zero + num).slice(-digit);
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      },
      // 获取截至日期
      getTime() {
        let cd = new Date();
        let date = this.zeroPadding(cd.getFullYear(), 4) + '-' + this.zeroPadding(cd.getMonth() + 1, 2) + '-' + this.zeroPadding(cd.getDate(), 2);
        let time = this.zeroPadding(cd.getHours(), 2) + ':' + this.zeroPadding(cd.getMinutes(), 2);
        return date + '  ' + time
      }
    },
    watch: {
      year(val) {
        this.getAPI(val);
      }
    }
  };
</script>

