          span
            position absolute
            top 8px
            left 180px
            color rgba(130, 216, 255, 0.7)
            font-size 0.16rem
        .c-bg
          position absolute
          width 100%
          height 100%
          z-index 0
          top 0
          left 0
          background url("../../assets/map-bg.png") no-repeat center bottom

        .map
          position relative
          top -40px
          left 30px
        .detail
          position absolute
          top 0.5rem
          left 0.4rem

          .allNumber
            font-size 0.2rem
            color #5aceff
            margin-bottom 0.2rem
            .bgNumber
              display inline-block
              ul
                li
                  float left
                  width 30px
                  line-height 50px
                  margin 0 2px
                  font-size 40px
                  font-weight 700
                  color #fff
                  border 2px solid #02adf5
                  border-radius 6px
                  text-align center
                  text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4
                  box-shadow 0 0 10px #276bf4
            .number
              font-size 0.5rem
              color #fff
              text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4
              margin-right 0.15rem

          p + p
            line-height 0.4rem
            color #ffffff
            font-size 0.16rem

            .number
              font-size 0.22rem
              color #128f9e
              margin-left 0.15rem
              vertical-align -0.04rem
              font-weight 700
      .w-right
        flex 1
        height 100%
        display flex
        flex-direction column
        .r-top
          flex 1.5
          height 100%
          .echarts
            width 100%
            height calc(100% - 0.28rem)
        .r-bottom
          flex 1
</style>
