<!--
  @auth:lcw
  云南地图+散点图
-->
<template>
    <div
      class="map-chart"
      v-loading="!loadFlag"
      :element-loading-background="$STANDARD.loadingBgColor"
      ref="chartEl"
      :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"
    >
    </div>
</template>

<script>
  import * as echarts from 'echarts'
  import {getmap_http} from "@/api/index";

  export default {
    name: 'yunnan',
    props: {
      params: Object
    },
    data() {
      return {
        myChart: null,
        rollFlag: true,
        loadFlag: false
      };
    },
    mounted() {
      this.init();
      this.$once('hook:beforeDestroy', () => {
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.myChart && this.myChart.dispose();
      })
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }

        if (this.params.data.length === 0) return;
        let options = this.params.options;
        // let geoCoordMap = {
        //   '定州市': [
        //     115.103812,
        //     38.493642
        //   ],
        //   '辛集市': [
        //     115.217451,
        //     37.92904
        //   ],
        //   '雄安新区': [
        //     116.020691,
        //     38.950646
        //   ],
        //   '石家庄市': [
        //     114.502461,
        //     38.045474
        //   ],
        //   '唐山市': [
        //     118.375393,
        //     39.635113
        //   ],
        //   '秦皇岛市': [
        //     119.186579,
        //     39.942531
        //   ],
        //   '邯郸市': [
        //     114.490686,
        //     36.612273
        //   ],
        //   '邢台市': [
        //     114.808851,
        //     37.3682
        //   ],
        //   '保定市': [
        //     115.182331,
        //     39.067657
        //   ],
        //   '张家口市': [
        //     114.884091,
        //     40.811901
        //   ],
        //   '承德市': [
        //     117.439152,
        //     41.476204
        //   ],
        //   '沧州市': [
        //     116.857461,
        //     38.310582
        //   ],
        //   '廊坊市': [
        //     116.604441,
        //     39.123927
        //   ],
        //   '衡水市': [
        //     115.665993,
        //     37.735097
        //   ]
        // };

        // function convertData(data) {
        //   var res = [];
        //   for (let i = 0; i < data.length; i++) {
        //     var geoCoord = geoCoordMap[data[i][options.xName]];
        //     if (geoCoord) {
        //       res.push({
        //         name: data[i].name,
        //         value: geoCoord.concat(data[i][options.yName]),
        //         visualMap: false
        //       });
        //     }
        //   }
        //   return res;
        // }
        getmap_http(`${this.params.options.JSON}.json`).then(json => {
          this.loadFlag = true;
          echarts.registerMap("guangdong", json.data);
          this.myChart = echarts.init(this.$refs.chartEl);
          let maxValue = 0, data = [];
          let dataKeys = Object.keys(options.map);
          let datavalues = Object.values(options.map);
          if (options.mockData) {
            for (let item of json.data.features.values()) {
              function random(lower, upper) {
                return Math.floor(Math.random() * (upper - lower + 1)) + lower;
              }

              let obj = {
                name: item.properties.name,
                value: random(1, 100)
              };
              data.push(obj);
              for (let item of data.values()) {
                maxValue = (maxValue > item.value ? maxValue : item.value)
              }
            }
          } else {
            for (let item of this.params.data.values()) {
              maxValue = (maxValue > item[options.yName] ? maxValue : item[options.yName]);
              let obj = {
                name: item[options.xName],
                value: item[options.yName],
                zlevel: 1
              };

              dataKeys.forEach((it, ind) => {
                obj[it] = item[it]
              });
              data.push(obj);
            }
          }

          this.option = {
            tooltip: {
              backgroundColor: "rgba(9, 39, 109, 0.8)",
              padding: [10, 20],
              formatter(params) {
                if (options.isHome) {
                  return `
                  <div class="tooltips">
                    <p><span class="number">${params.data[dataKeys[0]]}</span>件次</p>
                    <p><span class="name">${datavalues[1]}</span><span class="number">${params.data[dataKeys[1]]}</span></p>
                    <p><span class="name">${datavalues[2]}</span><span class="number">${params.data[dataKeys[2]]}</span></p>
                    <p><span class="name">${datavalues[3]}</span><span class="number">${params.data[dataKeys[3]]}</span></p>
                  </div>`;
                } else {
                  return params.name + ' : ' + params.value
                }
              }
            },
            geo: [
              {
                show: true,
                map: 'guangdong',
                aspectScale: 0.75,
                zoom: 1,
                label: {
                  normal: {
                    show: true,
                    formatter: (params) => {
                      if (params.value) {
                        return params.name;
                      }
                    },
                    position: 'bottom',
                    textStyle: {
                      color: "#000",
                      fontSize: 16
                    }
                  },
                  emphasis: {
                    textStyle: {
                      color: "#fff"
                    }
                  }
                },
                roam: false,
                itemStyle: {
                  normal: {
                    areaColor: '#fff',
                    shadowColor: '#122773',
                    shadowOffsetX: 15,
                    shadowOffsetY: 10
                  },
                  emphasis: {
                    areaColor: '#fefa82',
                    borderWidth: 0,
                    color: 'green',
                    label: {
                      show: false
                    }
                  }
                },
                z: 2
              },
            ],
            visualMap: {
              show: false,
              min: 0,
              max: maxValue,
              text: ["高", "低"], // 文本，默认为数值文本
              calculable: true,
              itemWidth: "12%",
              itemHeight: "110%",
              textStyle: {
                color: "#fff",
                fontSize: "14%"
              },
              bottom: "10%",
              left: "5%",
              inRange: {
                color: [
                  "#fff",
                  "#ffad83",
                  "#f2684f"
                  // "rgba(19,240,255,1)",
                  // "rgba(4,131,222,1)",
                  // "rgba(8,53,109,1)"
                ]
              }
            },
            series: [
              {
                type: 'map',
                map: 'guangdong',
                aspectScale: 0.75,
                zoom: 1,
                label: {
                  normal: {
                    show: false
                  },
                  emphasis: {
                    show: false
                  },
                },
                itemStyle: {
                  normal: {
                    borderColor: '#fff',
                    borderWidth: 1,
                    areaColor: '#12235c'
                  },
                  emphasis: {
                    areaColor: '#fefa82'
                  }
                },
                data: data
              },
              // 散点，暂时去掉
              // {
              //   name: 'scatter',
              //   // type: 'scatter',
              //   type: 'effectScatter',
              //   tooltip: {
              //     show: true,// 此处为false并不能直接去掉散点图tooltip，需使用formatter达到隐藏tooltip效果，原因不明，待解决
              //     formatter: params => {
              //       return ''
              //     }
              //   },
              //   coordinateSystem: 'geo',
              //   symbolSize: (val) => {
              //     // console.log(val[2]);
              //     return 10;
              //   },
              //   label: {
              //     normal: {
              //       show: true,
              //       formatter: (params) => {
              //         if (params.value) {
              //           return params.name;
              //         }
              //       },
              //       position: 'bottom',
              //       textStyle: {
              //         color: "#fff",
              //         fontSize: 16
              //       }
              //     },
              //     emphasis: {
              //       textStyle: {
              //         color: "#fff"
              //       }
              //     }
              //   },
              //   itemStyle: {
              //     normal: {
              //       color: '#3cfff8'
              //     },
              //     emphasis: {
              //       show: false,
              //       color: '#ffa400'
              //     }
              //   },
              //   data: convertData(this.params.data)
              // }
            ]
          };
          this.myChart.setOption(this.option);
          if (options.carousel) {
            this.chartMove();
          }
          // 鼠标点击
          // this.myChart.on("click", params => {
          //   this.$emit("change", params.name);
          // });
          let clickCurIndex = -1;
          this.myChart.on("click", params => {
            let name = params.name;
            // 未设置轮播时添加点击事件
            if (!(this.params.options.JSON==='440000')) {
              if (clickCurIndex !== params.dataIndex) {
                // 取消之前高亮的图形
                this.myChart.dispatchAction({
                  type: "downplay",
                  dataIndex: clickCurIndex
                });
                // 高亮当前图形
                this.myChart.dispatchAction({
                  type: "highlight",
                  dataIndex: params.dataIndex
                });
                clickCurIndex = params.dataIndex;
                name = params.name;
              } else {
                // 重复点击
                return
                // 取消之前高亮的图形
                // this.myChart.dispatchAction({
                //   type: "downplay",
                //   dataIndex: clickCurIndex
                // });
                // name = '全省';
              }
            }

            this.$emit("change", name);
          });

          // 鼠标移入
          this.myChart.on("mouseover", params => {
            if (options.carousel) {
              this.rollFlag = false;
              if (params.dataIndex === this.app.currentIndex) {
                // 序号相同不取消高亮
                return;
              } else {
                // 取消之前高亮的图形
                this.myChart.dispatchAction({
                  type: "downplay",
                  seriesIndex: 0,
                  dataIndex: this.app.currentIndex
                });
              }
            }
          });
          // 鼠标移除
          this.myChart.on("mouseout", params => {
            this.rollFlag = true;
          });
        });
      },
// 地图tooltip自动移动
      chartMove() {
        this.app = {};
        this.app.currentIndex = -1;

        if (this.interval) {
          clearInterval(this.interval);
        }
        this.interval = setInterval(() => {
          let dataLen = this.option.series[0].data.length;

          if (this.rollFlag) {
            // 取消之前高亮的图形
            this.myChart.dispatchAction({
              type: "downplay",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });

            this.app.currentIndex = (this.app.currentIndex + 1) % dataLen;
            // 高亮当前图形
            this.myChart.dispatchAction({
              type: "highlight",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
            this.myChart.dispatchAction({
              type: "showTip",
              seriesIndex: 0,
              dataIndex: this.app.currentIndex
            });
          }
        }, 2000);
      },
    },
    computed: {
      styleType() {
        return this.$store.state.styleType;
      },
      realData() {
        return this.params.data
      },
      // JSON() {
      //   return this.params.options.JSON
      // }
    },
    watch: {
      realData: {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      },
      // JSON: {
      //   handler(newVal, oldVal) {
      //     this.init();
      //   },
      //   deep: true
      // }
    }
  };
</script>

<style lang="stylus">
  .map-chart
    background url("./img/bg.png") no-repeat
  .tooltips
    p
      color rgba(19, 240, 255, 0.6)
      font-size 0.16rem

      .name
        margin-right 0.1rem
        color #fff

      .number
        text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4
        font-size 0.2rem
        color #fff
        margin-right 0.05rem

    p + p
      margin-top 0.1rem
      .number
        text-shadow none
        color #5aceff
</style>
